    })
  },
  watch: {
    '$store.state.lang' (value) {
      this.initLanguage()
    },
    'gameSetList.active' (active) {
      // Super彩球不載入列表，子帳號登入也不載入
      if (this.mapTabsActive[this.gameSetList.active] !== 's17') {
        if (this.loginType !== 'hlead') {
          this.loadAccountProfile()
          // 載入表格狀態(開始)
          this.loadingTableStatus = true
        }
      }
    },
    getAccountProfileState (payload) {
      /* 基本資訊 */
      if (this.accountInfoList.contents.length === 1 && payload.result) {
        // 帳號
        this.accountInfoList.contents[0].account = payload.result.l_no
        // 名稱
        this.accountInfoList.contents[0].name = payload.result.l_name
        // 體育額度
        this.accountInfoList.contents[0].quota.LCtotal.amount = payload.result.quota.LCtotal
        // BingoStar額度** (API 未給相對應的參數)
        this.accountInfoList.contents[0].quota.bingo.amount = payload.result.quota.bingo
        // 皇家額度
        this.accountInfoList.contents[0].quota.royal.amount = payload.result.quota.royal
        // 是否允許投注(0：允許投注 1：不允許投注)
        this.accountInfoList.contents[0].allowLogin = payload.result.l_close === 0 ? '是' : '否'
        // 球類佔成
        this.accountInfoList.contents[0].occupy = payload.result.occupy
      }

      /* 球類設定資訊 */
      for (let i in this.gameSetList.contents) {
        if ((this.gameSetList.contents[i].type === this.mapTabsActive[this.gameSetList.active]) &&
            !common.isEmpty(payload.result)) {
          // 若 gameSet 為 null
          if (!payload.result.gameSet) return
          // 體育遊戲的足球或世足左側有六個標題
          if (this.gameSetList.contents[i].category === 'sport' && (this.gameSetList.contents[i].type === 's10' || this.gameSetList.contents[i].type === 's15')) {
            let newTableContent = []
            newTableContent[0] = this.compositeNewKeyItems(payload.result.gameSet.commission_A)
            newTableContent[1] = this.compositeNewKeyItems(payload.result.gameSet.commission_B)
            newTableContent[2] = this.compositeNewKeyItems(payload.result.gameSet.commission_C)
            newTableContent[3] = this.compositeNewKeyItems(payload.result.gameSet.LCtotal)
            newTableContent[4] = this.compositeNewKeyItems(payload.result.gameSet.teamLCtotal)
            // @不顯示單邊上限@
            // newTableContent[5] = this.compositeNewKeyItems(payload.result.gameSet.allLCtotal, payload.result.gameSet.fu_limit)
            this.gameSetList.contents[i].tableContent = newTableContent
          // 一般體育遊戲有四個標題
          } else if (this.gameSetList.contents[i].category === 'sport' && (this.gameSetList.contents[i].type !== 's10' && this.gameSetList.contents[i].type !== 's15')) {
            let newTableContent = []
            newTableContent[0] = this.compositeNewKeyItems(payload.result.gameSet.commission)
            newTableContent[1] = this.compositeNewKeyItems(payload.result.gameSet.LCtotal)
            newTableContent[2] = this.compositeNewKeyItems(payload.result.gameSet.teamLCtotal)
            // @不顯示單邊上限@
            // newTableContent[3] = this.compositeNewKeyItems(payload.result.gameSet.allLCtotal, payload.result.gameSet.fu_limit)
            this.gameSetList.contents[i].tableContent = newTableContent
          // 外接遊戲 BingoStar 與皇家有一個標題
          } else if (this.gameSetList.contents[i].category === 'external') {
            /* 表格一 */
            // BingoStar
            if (payload.result.gameSet.hasOwnProperty('bingo')) {
              let newTableContent = [
                {
                  super_big_small: '',
                  super_odd_even: '',
                  super_guess: '',
                  normal_big_small_tie: '',
                  normal_odd_even_draw: '',
                  elements: '',
                  seasons: '',
                  star: '',
                  other_fanbodan: ''
                }
              ]
              // 由於回傳資料有另一張表[各玩法下注限額]內容，所以只能一個一個綁定
              newTableContent[0].super_big_small = payload.result.gameSet.bingo.super_big_small
              newTableContent[0].super_odd_even = payload.result.gameSet.bingo.super_odd_even
              newTableContent[0].super_guess = payload.result.gameSet.bingo.super_guess
              newTableContent[0].normal_big_small_tie = payload.result.gameSet.bingo.normal_big_small_tie
              newTableContent[0].normal_odd_even_draw = payload.result.gameSet.bingo.normal_odd_even_draw
              newTableContent[0].seasons = payload.result.gameSet.bingo.seasons
              newTableContent[0].elements = payload.result.gameSet.bingo.elements
              newTableContent[0].other_fanbodan = payload.result.gameSet.bingo.other_fanbodan
              newTableContent[0].star = payload.result.gameSet.bingo.star
              this.gameSetList.contents[i].tableContent = newTableContent
            }
            // 皇家
            if (payload.result.gameSet.hasOwnProperty('royal') && payload.result.gameSet.royal) {
              let newTableContent = [
                {
                  bacc: '',
                  fanTan: '',
                  lunPan: '',
                  shaiZi: '',
                  longHu: '',
                  insuBacc: ''
                }
              ]
              // 由於回傳資料有另一張表[押分限注編號]內容，所以只能一個一個綁定
              newTableContent[0].bacc = payload.result.gameSet.royal.bacc
              newTableContent[0].fanTan = payload.result.gameSet.royal.fanTan
              newTableContent[0].lunPan = payload.result.gameSet.royal.lunPan
              newTableContent[0].shaiZi = payload.result.gameSet.royal.shaiZi
              newTableContent[0].longHu = payload.result.gameSet.royal.longHu
              newTableContent[0].insuBacc = payload.result.gameSet.royal.insuBacc
              this.gameSetList.contents[i].tableContent = newTableContent
            }

            /* 表格二 */
            // BingoStar
            if (payload.result.gameSet.hasOwnProperty('bingo')) {
              let newTableContent2 = [
