                }
              ]
              newTableContent2[0].super_big_small = `${payload.result.gameSet.bingo.bet_min_super_big_small} - ${payload.result.gameSet.bingo.bet_max_super_big_small}`
              newTableContent2[0].super_odd_even = `${payload.result.gameSet.bingo.bet_min_super_odd_even} - ${payload.result.gameSet.bingo.bet_max_super_odd_even}`
              newTableContent2[0].super_guess = `${payload.result.gameSet.bingo.bet_min_super_guess} - ${payload.result.gameSet.bingo.bet_max_super_guess}`
              newTableContent2[0].normal_big_small_tie = `${payload.result.gameSet.bingo.bet_min_normal_big_small_tie} - ${payload.result.gameSet.bingo.bet_max_normal_big_small_tie}`
              newTableContent2[0].normal_odd_even_draw = `${payload.result.gameSet.bingo.bet_min_normal_odd_even_draw} - ${payload.result.gameSet.bingo.bet_max_normal_odd_even_draw}`
              newTableContent2[0].seasons = `${payload.result.gameSet.bingo.bet_min_seasons} - ${payload.result.gameSet.bingo.bet_max_seasons}`
              newTableContent2[0].elements = `${payload.result.gameSet.bingo.bet_min_elements} - ${payload.result.gameSet.bingo.bet_max_elements}`
              newTableContent2[0].other_fanbodan = `${payload.result.gameSet.bingo.bet_min_other_fanbodan} - ${payload.result.gameSet.bingo.bet_max_other_fanbodan}`
              newTableContent2[0].star = `${payload.result.gameSet.bingo.bet_min_star} - ${payload.result.gameSet.bingo.bet_max_star}`
              this.gameSetList.contents[i].tableContent2 = newTableContent2
            }
            // 皇家
            if (payload.result.gameSet.hasOwnProperty('royal') && payload.result.gameSet.royal) {
              let newTableContent2 = [
                {
                  royalLimitLeve1: '',
                  royalLimitLeve2: '',
                  royalLimitLeve3: '',
                  royalLimitLeve4: '',
                  royalLimitLeve5: '',
                  royalLimitLeve6: '',
                  royalLimitLeve7: '',
                  royalLimitLeve8: '',
                  royalLimitLeve9: '',
                  royalLimitLeve10: '',
                  royalLimitLeveLink: '押分限注編號說明'
                }
              ]
              for (let i in payload.result.gameSet.royal.limit_level.split(',')) {
                newTableContent2[0][`royalLimitLeve${payload.result.gameSet.royal.limit_level.split(',')[i]}`] = payload.result.gameSet.royal.limit_level.split(',')[i]
              }
              this.gameSetList.contents[i].tableContent2 = newTableContent2
            }
          }
          // 設定 header 過關數量
          for (let c in this.gameSetList.headers[this.gameSetList.contents[i].type]) {
            if (this.gameSetList.headers[this.gameSetList.contents[i].type][c].value === 'fu') {
              this.gameSetList.headers[this.gameSetList.contents[i].type][c].count = payload.result.gameSet.fu_limit
            }
          }
        }
      }

      // 表格加入最左側名稱
      for (let a in this.gameSetList.contents) {
        for (let b in this.gameSetList.contents[a]) {
          if (b === 'tableContent' && (this.gameSetList.contents[a].type !== 's10' && this.gameSetList.contents[a].type !== 's15')) { // 一般體育遊戲最左側標題
            if (this.gameSetList.contents[a].tableContent.length >= 1) this.gameSetList.contents[a].tableContent[0].name = this.gameSetList.nameItems[0]
            if (this.gameSetList.contents[a].tableContent.length >= 2) this.gameSetList.contents[a].tableContent[1].name = this.gameSetList.nameItems[1]
            if (this.gameSetList.contents[a].tableContent.length >= 3) this.gameSetList.contents[a].tableContent[2].name = this.gameSetList.nameItems[2]
            // if (this.gameSetList.contents[a].tableContent.length >= 4) this.gameSetList.contents[a].tableContent[3].name = this.gameSetList.nameItems[3]
          } else if (b === 'tableContent' && (this.gameSetList.contents[a].type === 's10' || this.gameSetList.contents[a].type === 's15')) { // 體育遊戲的足球與世足最左側標題
            if (this.gameSetList.contents[a].tableContent.length >= 1) this.gameSetList.contents[a].tableContent[0].name = this.gameSetList.nameItemsF[0]
            if (this.gameSetList.contents[a].tableContent.length >= 2) this.gameSetList.contents[a].tableContent[1].name = this.gameSetList.nameItemsF[1]
            if (this.gameSetList.contents[a].tableContent.length >= 3) this.gameSetList.contents[a].tableContent[2].name = this.gameSetList.nameItemsF[2]
            if (this.gameSetList.contents[a].tableContent.length >= 4) this.gameSetList.contents[a].tableContent[3].name = this.gameSetList.nameItemsF[3]
            if (this.gameSetList.contents[a].tableContent.length >= 4) this.gameSetList.contents[a].tableContent[4].name = this.gameSetList.nameItemsF[4]
            // if (this.gameSetList.contents[a].tableContent.length >= 4) this.gameSetList.contents[a].tableContent[5].name = this.gameSetList.nameItemsF[5]
          } else if (b === 'tableContent2' && this.gameSetList.contents[a].type === 's16') { // BingoStar有第二個表格
            if (this.gameSetList.contents[a].tableContent2.length >= 1) this.gameSetList.contents[a].tableContent2[0].name = this.gameSetList.nameItemsB[0]
          } else if (b === 'tableContent2' && this.gameSetList.contents[a].type === 's18') { // 皇家視訊有第二個表格
            if (this.gameSetList.contents[a].tableContent2.length >= 1) this.gameSetList.contents[a].tableContent2[0].name = this.gameSetList.nameItemsR[0]
          }
        }
      }

      // console.log('球類設定資訊: ', this.gameSetList.contents)
    },
    getDashboardInfoState () {
      this.updateDashboardInfo()
    }
  },
  created () {
    if (sessionStorage.getItem('super.manage.login.type')) this.loginType = sessionStorage.getItem('super.manage.login.type')
  },
  mounted () {
    this.initLanguage()

    // 由於表格的上一頁下一頁樣式不見了，透過以下方式加模板設定的 class 樣式
    // * 之後需要調整表格的上一頁下一頁樣式 *
    // $('.v-btn').addClass('success')

    // 設置修改密碼帳號
    this.passwordItems.account = this.getProfileState.hasOwnProperty('result') ? this.getProfileState.result.l_no : sessionStorage.getItem('super.manage.account')
    // 設置修改密碼帳號類別
    this.passwordItems.accountType = sessionStorage.getItem('super.manage.login.type')

    // 載入狀態(開始)
    this.loadingStatus = true

    this.updateDashboardInfo()
  },
  methods: {
    ...mapActions({
      getAccountProfile: 'api/getAccountProfile'
    }),
    toBackTab () {
      const active = parseInt(this.gameSetList.active)
      this.gameSetList.active = (active < (this.gameSetList.contents.length + 1) ? active - 1 : 0)
      if (active === 0) this.gameSetList.active = this.gameSetList.contents.length - 1
    },
    toNextTab () {
      const active = parseInt(this.gameSetList.active)
      this.gameSetList.active = (active < (this.gameSetList.contents.length - 1) ? active + 1 : 0)
    },
    // 載入管端帳號基本資訊
    loadAccountProfile () {
      let balltype = this.mapTabsActive[this.gameSetList.active].split('s')[1]
      // balltype 於 GET API 改參數值
      if (this.mapTabsActive[this.gameSetList.active].split('s')[1] === '16') balltype = 'bingo'
      if (this.mapTabsActive[this.gameSetList.active].split('s')[1] === '18') balltype = 'royal'
      try {
        this.getAccountProfile({ balltype: balltype })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(`取得管端帳號資訊錯誤`)
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得管端帳號資訊成功`)
              }
            }
            // 載入狀態(結束)
            this.loadingStatus = false
            // 載入表格狀態(結束)
            this.loadingTableStatus = false
          })
          .catch(err => {
            console.error(err)
            // 載入狀態(結束)
            this.loadingStatus = false
            // 載入表格狀態(結束)
            this.loadingTableStatus = false
          })
      } catch (error) {
        this.$toastr.error(`站台資料錯誤，請聯絡客服`)
        console.error(error)
      }
    },
    /**
     * 小數值轉換百分比
     * @params point   小數值
     * @params number  轉換後小數位數
     **/
    toPercent (point, number) {
      return common.toPercent(point, number)
    },
    /**
     * 以_切割名稱取得第0位置名稱組合新 key 物件資料
     * @params items    需解析物件資料
     * @params fuLimit  過關數量
     **/
    compositeNewKeyItems (items, fuLimit) {
      let newItems = {}
      for (let name in items) {
        newItems[name.split('_')[0]] = items[name]
        // 設定 content 單邊上限過關數量
        if (fuLimit) newItems.fu_limit = fuLimit
      }
      return newItems
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo &&
          dashboardInfo.hasOwnProperty('noOpenGame') &&
          dashboardInfo.hasOwnProperty('showItems')) {
        // 不開放遊戲設定(由於帳號基本資訊內容，前端是另外設定變數綁定表格值，所以表格內容遊戲開關可由前端做)
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }

        // 是否顯示修改密碼功能
        this.passwordItems.show = dashboardInfo.showItems.editPassword
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      // 帳號基本資訊表格 header
      let accountInfoListHeadersItems = []
      for (let i in this.accountInfoList.headers) {
        if (this.accountInfoList.headers[i].type !== type) accountInfoListHeadersItems.push(this.accountInfoList.headers[i])
      }
      this.accountInfoList.headers = accountInfoListHeadersItems

      // 帳號基本資訊表格內容
      switch (type) {
        // 關閉BingoStar
        case 's16':
          this.accountInfoList.contents[0].quota.bingo.show = false
          break
        // 關閉皇家視訊
        case 's18':
          this.accountInfoList.contents[0].quota.royal.show = false
          break
      }

      // 球類設定資訊 tab 分頁
      let mapTabsActiveItems = []
      for (let a in this.mapTabsActive) {
        if (this.mapTabsActive[a] !== type) mapTabsActiveItems.push(this.mapTabsActive[a])
      }
      this.mapTabsActive = mapTabsActiveItems

      // 球類設定資訊表格 header
      delete this.gameSetList.headers[type]

      // 球類設定資訊表格內容
      let gameSetListContentsItems = []
      for (let b in this.gameSetList.contents) {
        if (this.gameSetList.contents[b].type !== type) gameSetListContentsItems.push(this.gameSetList.contents[b])
      }
      this.gameSetList.contents = gameSetListContentsItems
    },
    // 初始語系
    initLanguage () {
      // 說明
      // 越南站(22站)不顯示「元」單位
      this.desc = (!common.isVietnamStation()) ? `說明：如果「返水」為 100，表示每下注一萬元可以得到返水 100 元` : `說明：如果「返水」為 100，表示每下注一萬可以得到返水 100`
    }
  }
}
</script>

<style lang="scss">
  .profile {
    // 靠左100px
    .left100 {
      padding-left: 100px !important;
    }
    // 最左欄位固定
    .leftmostFixed {
      position: absolute;
      background-color: white;
      width: 110px !important;
    }
  }
</style>
